@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

:root {
  --primary: #bc4123;
  --secondary: #2b3452;
}

/* hide scrollbar  */
html {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

body {
  margin: 0;
  padding: 20px;
  font-family: "Poppins";
  background: var(--secondary);
}

* {
  color: #fff;
  margin: 0;
}

p {
  margin: 20px 0;
}

button {
  border: 0;
  padding: 8px;
  border-radius: 4px;
  color: white;
  background: var(--primary);
  cursor: pointer;
}

/* navbar */
header nav {
  display: flex;
  gap: 16px;
  justify-content: end;
  max-width: 1200px;
  margin: 0 auto;
}

header nav h1 {
  margin-right: auto;
  border-bottom: 3px solid var(--primary);
}

header nav a {
  text-decoration: none;
  padding: 6px;
  border-radius: 4px;
}

header nav a.active {
  background-color: var(--primary);
}


/* page content */
main{
  max-width: 1200px;
  margin: 40px auto;
}

/* help layout */
.help-layout nav{
  display: flex;
  justify-content: center;
  gap: 30px;
  margin: 30px 0;
}

.help-layout nav a{
  padding: 10px;
  border: 2px solid #fff;
  border-radius: 4px;
  text-decoration: none;
}

.help-layout nav a:hover{
  border-color: var(--primary);
}

.help-layout nav a.active{
  background-color: var(--primary);
}

/* faq */
.faq .question {
  background: rgba(0, 0, 0, .2);
  padding: 5px 20px;
  border-radius: 4px;
  margin: 20px 0;
}

/* contact form  */
form input,
form label span,
form textarea {
  display: block;
}

form input, form textarea{
  margin-bottom: 30px;
  padding: 8px;
  border: 0;
  outline: none;
  border-radius: 4px;
  width: 300px;
  color: var(--secondary)
}

form label span {
  margin-bottom: 10px 0;
}

p.error {
  color: red;
  font-size: .8rem;
}


/* careers list */
.careers > a {
  display: block;
  background-color: rgba(0, 0, 0, .2);
  padding: 20px;
  border-radius: 4px;
  margin: 20px 0;
  text-decoration: none;
}

.careers > a:hover p {
  color: var(--primary)
}

.careers p{
  margin: 0;
}

.careers > a p:last-child {
  font-size: 0.8em;
  color: #bbb;
}

/* careers details */
.career-details {
  background-color: rgba(0, 0, 0, .2);
  padding: 20px;
  border-radius: 4px;
  margin: 20px 0;
}


/* breadcrumbs */
.breadcrumbs {
  max-width: 1200px;
  margin: 20px auto;
}

.breadcrumbs > * {
  display: inline-block;
  margin-right: 10px;
}

.breadcrumbs .crumb:after {
  content: '>';
  margin-left: 10px;
}

.breadcrumbs .crumb:last-child::after {
  display: none;
}

.breadcrumbs .crumb a {
  color: #ddd;
}

